<template>
  <div class="article_detail">
    <div class="title">
      <van-nav-bar
        :title="title"
        left-text="返回"
        left-arrow
        @click-left="returnList"
      />
    </div>
    <div class="content">
      <div class="about">
        <van-tag type="warning">#雪苞</van-tag>
        <van-tag type="warning">#金炫廷</van-tag>
        <van-tag type="warning">#金知妍</van-tag>
      </div>
      <div ref="area" class="area"></div>
    </div>
  </div>
</template>

<script>
import data from "../../assets/data/article.js";
export default {
  data() {
    return {
      title: "",
    };
  },
  methods: {
    getArticle() {
      data.map((item) => {
        if (item.id === this.$route.query.id) {
          this.title = item.title;
          this.$refs.area.innerHTML = item.area;
        }
      });
    },
    returnList() {
      this.$router.push("/article");
    },
  },
  created() {},
  mounted() {
    this.getArticle();
  },
};
</script>

<style lang="less" scoped>
.article_detail {
  position: relative;
  .title {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 9999;
  }
  .content {
    padding: 20px;
    margin-top: 46px;

    .about {
      margin-bottom: 10px;

      span {
        margin-right: 4px;
      }
    }

    .area {
      font-size: 14px;
      line-height: 20px;
      background: #fafafa;
      padding: 20px;
      text-indent: 2rem;
    }
  }
}
</style>