<template>
  <div class="article">
    <div class="title">
      <van-nav-bar title="文章列表" />
    </div>

    <div class="content">
      <div class="swiper">
        <van-swipe :autoplay="3000" height="150">
          <van-swipe-item v-for="(image, index) in images" :key="index">
            <img :src="image" alt="">
          </van-swipe-item>
        </van-swipe>
      </div>
      <van-card
        v-for="(item, index) in articleList"
        :key="index"
        :tag="item.isNew == 1 ? '新' : ''"
        :desc="item.createTime"
        :title="item.title"
        :thumb="item.img"
      >
        <template #tags>
          <van-tag
            style="margin-right: 2px"
            v-for="(k, l) in item.tagList"
            :key="l"
            type="warning"
            >{{ k }}</van-tag
          >
          <div class="detail" @click="toDetail(item.id)">
            <span>前往阅读</span>
            <van-icon name="arrow" />
          </div>
        </template>
      </van-card>
    </div>
  </div>
</template>

<script>
import data from "../../assets/data/article.js";
export default {
  data() {
    return {
      articleList: [],
      images: [
        require('../../assets/static/swiper/swiper1.jpg'),
        require('../../assets/static/swiper/swiper2.jpg'),
        require('../../assets/static/swiper/swiper3.jpg'),
        require('../../assets/static/swiper/swiper4.jpg'),
        require('../../assets/static/swiper/swiper5.jpg'),
      ],
    };
  },
  methods: {
    getArticleList() {
      this.articleList = data;
    },
    toDetail(id) {
      this.$router.push("/article/detail?id=" + id);
    },
  },
  created() {
    this.getArticleList();
  },
};
</script>

<style lang="less" scoped>
.article {
  position: relative;
  .title {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 9999;
  }
  .content {
    padding: 20px;
    margin: 46px 0 50px 0;

    .swiper {
      margin-bottom: 10px;

      img {
        width: 100%;
      }
    }

    .detail {
      position: absolute;
      right: 0;
      bottom: 0;
      color: #323233;
      cursor: pointer;

      span,
      i {
        vertical-align: middle;
      }
    }
  }
}
</style>